* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

.galleries {
    padding: 2rem;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2rem;
}

.gallery {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    grid-gap: 20px;
    align-items: stretch;
    background: white;
    padding: 2rem;
}

.gallery img {
    width: 100%;
    object-fit: cover;
    border: 1px solid black;
}

.modal {
    position: fixed;
    background: rgba(0, 0, 0, 0.5);
    z-index: 2;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: grid;
    align-items: center;
    justify-items: center;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.5s;
}

.modalInner {
    border-radius: 4px;
    background: white;
    box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.05);
    transform: translateY(-100vh);
    transition: all 0.5s ease-in-out;
    max-width: 1000px;
    height: calc(100vh - 100px);
    display: grid;
    grid-template-columns: 50px 1fr 50px;
    color: black;
    margin: 3rem;
    -webkit-transition: all 0.5s ease-in;
    -moz-transition: all 0.5s ease-in;
    -ms-transition: all 0.5s ease-in;
    -o-transition: all 0.5s ease-in;
}

.modal figure {
    height: 100%;
    display: grid;
    margin: 0;
    grid-template-rows: 1fr auto;
}

.modal img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.modal.open {
    opacity: 1;
    pointer-events: all;
}

.modal figcaption {
    padding: 10px;
}

.modal h2 {
    color: black;
}

.modal.open .modalInner {
    transform: translateY(0);
}